/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root{
	font-family: Arial, Helvetica, sans-serif;
}

.game-container{
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 15vh;
}

.tiles-container{
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.tiles{
    font-size: 64px;
    width: clamp(100px, 10vh, 240px);
	height: clamp(100px, 10vh, 240px);
    background-color: rgb(177, 219, 255);
    box-shadow: none;
    border: 3px solid rgb(17, 14, 48);
    border-radius: 2vh;
}

.clicked{
    opacity: 0;
}

.ready-tiles{
	font-size: 0;
}

button:disabled{
	color: black;
}



.buttons{
	display: flex;
	justify-content: center;
	gap: 50px;
}

.increase-level-button{
	font-size: 36px;
	width: clamp(72px, 8vh, 240px);
	height: clamp(72px, 8vh, 240px);
	display: grid;
	place-items: center;
    border: 3px solid rgb(17, 14, 48);
    border-radius: 2vh;
}

.increase-level-button:disabled{
	background-color: rgb(223, 223, 223);
}

.increase-level-button:enabled{
	background-color: rgb(165, 255, 159);
	animation: pop-out 0.4s 1 0s;
}

@keyframes pop-out {
	from {
		transform: scale(100%);
	}
	50%{
		transform: scale(105%)
	}
	to {
		transform: scale(100%);
	}
}

.reset-button{
	background-color: rgb(223, 97, 97);
	font-size: 36px;
	width: clamp(144px, 8vh, 480px);
	height: clamp(72px, 8vh, 240px);
	box-shadow: none;
    border: 3px solid rgb(17, 14, 48);
    border-radius: 2vh;
}

.ready-button{
	font-size: 48px;
	width: clamp(240px, 20vh, 480px);
	height: clamp(72px, 10vh, 240px);
	box-shadow: none;
    border: 3px solid rgb(17, 14, 48);
    border-radius: 2vh;
}

.ready-button:disabled{
	background-color: rgb(223, 223, 223);
}

.ready-button:enabled{
	background-color: rgb(165, 255, 159);
	animation: pop-out 0.4s 1 0s;
}

.instructions-container{
	position: fixed;
	top: 1vw;
	left: 1vw;
	z-index: 2;
}

.instructions-header{
	font-size: 24px;
	width: clamp(240px, 20vh, 480px);
	height: clamp(72px, 6vh, 240px);
	display: grid;
	place-items: center;
	box-shadow: none;
    border: 3px solid rgb(17, 14, 48);
    border-radius: 2vh;
	background-color: rgb(255, 255, 255);
}

.instructions-welcome{
	font-size: 24px;
}

.instructions-description{
	font-size: 18px;
	list-style-type: circle;
}


.lost{
	font-size: 46px;
	background-color: white;
	border: 2px solid black;
	padding: 96px;
	position: absolute;
	z-index: 2;
	display: grid;
	place-items: center;
	animation: pop-out 0.4s 1 0s;
}